:root {
  --mdb-font-roboto: "Roboto", sans-serif;
}

body {
  font-family: var(--mdb-font-roboto);
  line-height: 1.6;
  color: #4f4f4f;
}

a {
  text-decoration: none;
}

button:focus {
  outline: 0;
}

.note {
  padding: 10px;
  border-left: 6px solid;
  border-radius: 5px;
}
.note strong {
  font-weight: 600;
}
.note p {
  font-weight: 500;
}

.note-primary {
  background-color: #c5eed1;
  border-color: #30A853;
}

.note-secondary {
  background-color: #7d8199;
  border-color: #30323D;
}

.note-success {
  background-color: #edfaf0;
  border-color: #30A853;
}

.note-danger {
  background-color: #fee3e8;
  border-color: #f93154;
}

.note-warning {
  background-color: #fff1d6;
  border-color: #ffa900;
}

.note-info {
  background-color: #acf5f5;
  border-color: #1acfcf;
}

.note-light {
  background-color: #F5F6FB;
  border-color: #262626;
}

@media (min-width: 1199px) {
  .w-responsive {
    width: 75%;
  }
}

.bg-primary {
  background-color: #30A853 !important;
}

.bg-secondary {
  background-color: #30323D !important;
}

.bg-success {
  background-color: #30A853 !important;
}

.bg-info {
  background-color: #1acfcf !important;
}

.bg-warning {
  background-color: #ffa900 !important;
}

.bg-danger {
  background-color: #f93154 !important;
}

.bg-light {
  background-color: #F5F6FB !important;
}

.bg-dark {
  background-color: #262626 !important;
}

.bg-white {
  background-color: #FFFFFF !important;
}

.bg-black {
  background-color: #000000 !important;
}

.shadow-0 {
  box-shadow: none !important;
}

.shadow-1 {
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.05) !important;
}

.shadow-2 {
  box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.05) !important;
}

.shadow-3 {
  box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.05) !important;
}

.shadow-4 {
  box-shadow: 0 15px 30px 0 rgba(0, 0, 0, 0.05) !important;
}

.shadow-5 {
  box-shadow: 0 20px 40px 0 rgba(0, 0, 0, 0.05) !important;
}

.shadow-1-strong {
  box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.25), 0px 3px 10px 0px rgba(0, 0, 0, 0.2) !important;
}

.shadow-2-strong {
  box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.25), 0px 5px 15px 2px rgba(0, 0, 0, 0.2) !important;
}

.shadow-3-strong {
  box-shadow: 0px 6px 11px 0px rgba(0, 0, 0, 0.25), 0px 7px 20px 3px rgba(0, 0, 0, 0.2) !important;
}

.shadow-4-strong {
  box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.25), 0px 10px 30px 4px rgba(0, 0, 0, 0.2) !important;
}

.shadow-5-strong {
  box-shadow: 0px 6px 20px 0px rgba(0, 0, 0, 0.25), 0px 12px 40px 5px rgba(0, 0, 0, 0.2) !important;
}

