.form-check-input {
  position: relative;
  width: 1.125rem;
  height: 1.125rem;
  background-color: #FFFFFF;
  border: 0.125rem solid #757575;
  margin-top: 0;
}
.form-check-input:before {
  content: "";
  position: absolute;
  box-shadow: 0px 0px 0px 13px transparent;
  border-radius: 50%;
  width: 0.875rem;
  height: 0.875rem;
  background-color: transparent;
  opacity: 0;
  pointer-events: none;
  transform: scale(0);
}
.form-check-input:hover {
  cursor: pointer;
}
.form-check-input:hover:before {
  opacity: 0.04;
  box-shadow: 0px 0px 0px 13px rgba(0, 0, 0, 0.6);
}
.form-check-input:focus {
  box-shadow: none;
  border-color: #757575;
  transition: border-color 0.2s;
}
.form-check-input:focus:before {
  opacity: 0.12;
  box-shadow: 0px 0px 0px 13px rgba(0, 0, 0, 0.6);
  transform: scale(1);
  transition: box-shadow 0.2s, transform 0.2s;
}
.form-check-input:checked {
  border-color: #30A853;
}
.form-check-input:checked:before {
  opacity: 0.16;
}
.form-check-input:checked:after {
  content: "";
  position: absolute;
}
.form-check-input:checked:focus {
  border-color: #30A853;
}
.form-check-input:checked:focus:before {
  box-shadow: 0px 0px 0px 13px #30A853;
  transform: scale(1);
  transition: box-shadow 0.2s, transform 0.2s;
}
.form-check-input:indeterminate:focus:before {
  box-shadow: 0px 0px 0px 13px #30A853;
}
.form-check-input[type=checkbox] {
  border-radius: 0.125rem;
}
.form-check-input[type=checkbox]:focus:after {
  content: "";
  position: absolute;
  width: 0.875rem;
  height: 0.875rem;
  z-index: 1;
  display: block;
  border-radius: 0;
  background-color: #FFFFFF;
}
.form-check-input[type=checkbox]:checked {
  background-image: none;
  background-color: #30A853;
}
.form-check-input[type=checkbox]:checked:after {
  display: block;
  transform: rotate(45deg) /*!rtl:ignore*/;
  border-width: 0.125rem;
  border-color: #FFFFFF;
  width: 0.375rem;
  height: 0.8125rem;
  border-style: solid;
  border-top: 0;
  border-left: 0 /*!rtl:ignore*/;
  margin-left: 0.25rem;
  margin-top: -1px;
  background-color: transparent;
}
.form-check-input[type=checkbox]:checked:focus {
  background-color: #30A853;
}
.form-check-input[type=checkbox]:indeterminate {
  background-image: none;
  background-color: transparent;
  border-color: #757575;
}
.form-check-input[type=checkbox]:indeterminate:after {
  display: block;
  transform: rotate(90deg) /*!rtl:ignore*/;
  border-width: 0.125rem;
  border-color: #FFFFFF;
  width: 0.125rem;
  height: 0.875rem;
  border-style: solid;
  border-top: 0;
  border-left: 0 /*!rtl:ignore*/;
  margin-left: 0.375rem;
  margin-top: 0;
}
.form-check-input[type=checkbox]:indeterminate:focus {
  background-color: #30A853;
  border-color: #30A853;
}
